
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
XHeader
},
data() {
return {
noData: false,
list: []
};
},
mounted() {
this.getList();
},
methods: {
getList() {
this.$vux.loading.show({
text: "加载中..."
});
this.$ajax
.post(this.$root.host, {
"MSH.1": "server",
"MSH.2": "agreementServiceList"
})
.then(res => {
console.log(res);
this.$vux.loading.hide();
if (typeof res != "undefined" && res.length > 0) {
this.list = res;
this.noData = false;
} else {
this.noData = true;
}
})
.catch(error => {
console.log(error);
this.$vux.loading.hide();
});
},
gotoDetail(CBM) {
this.$router.push({ name: "serAgreeDetail", params: { CBM: CBM } });
}
}
};



